.section-five{
    height: auto;
    padding-bottom: 10px;  
    background-image: url('../assets/images/freedive.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border-top: solid 1px black;
    background-attachment: fixed; 
}

.section-five ul {
    list-style: none; 
    padding-left: 0; 
}

.section-five li {
    width: 275px;
    height: 200px;
    margin: 30px 20px;
    text-align: center;
    font-family: 'Indie Flower', cursive;
    background-color: white;
    height: 271px;
    -webkit-transition: height 1s ease;
    -o-transition: height 1s ease;
    transition: height 1s ease;
    padding: 10px 10px;
    margin: 40px auto;
    overflow: hidden;
}

.section-five li#extend{
    height: 550px; 
}

.section-five p {
    font-family: 'Indie Flower', cursive;
}

.section-five img {
    width: 275px;
    height: 220px;
}


