.section-seven{
    height: auto;
    padding-bottom: 40px;   
    padding-top:40px; 
    background-image: url('../assets/images/freedive.jpg');
    background-attachment: fixed; 
    background-size: 100% 100%; 
    border-top: solid 1px white; 
}

.section-seven ul {
    padding-left: 0; 
    list-style: none; 
}

.section-seven li {
    width: 282px;
    background-color: white;
    margin: 20px auto;
    text-align: center;
    padding: 20px 5px;
    border-radius: 1px; 
}

.section-seven img {
    width: 250px;
    height: 250px;
}


