.app-header {
  background-color: #222;
  height: 59px;
  color: white;
  border-top: solid 1px black; 
  position: fixed; 
  z-index: 1000;
  right: 0;
  left: 0;
   }

.app-header h2{
    margin: 13px 0 0 20px;
}

.app-header img {
    width: 40px;
    height: 29px;
    background-color: #00e6ff;
    position: absolute;
    right: 26px;
    top: 12px;
    border-radius: 2px;    
}

.app-header nav {
    position: absolute;
    top: 51px;
    right: 0px;
    background-color: #222;
    width: 187px;
    text-align: center;
    border-radius: 1px;
    height: 0px; 
    -webkit-transition: height 2s ease; 
    -o-transition: height 2s ease; 
    transition: height 2s ease; 
    overflow: hidden; 
}

#nav-height-transition{
    height: 248px; 
}

.app-header ul {
    padding-left: 0;
    list-style:  none; 
}

.app-header li{
    padding: 20px 0;
    margin: 10px 13px;
    background-color: rgba(99, 99, 99, 0.67);
}

.app-header a {
    text-decoration:  none; 
    color: white; 
}

.app-header a:hover{
    color: blue; 
}

@media(min-width:768px){
    .app-header {
    height: 70px;
    }     
.app-header h2 {
    margin: 21px 0 0 31px;
}    
.app-header img {
     right: 48px;
    top: 21px;
}
}

@media(min-width: 1024px){
    .app-header {
        right: 155px;
        left: 155px; 
    }
}


