.section-one {
    background-image: url('../assets/images/freedive.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% 100%;
    width: 100%;
    height: 667px;
    border-top: solid 1px black; 
}


.welcome-section {
    margin: 0 13px;
    position: absolute;   
    top: -100%;
    right: 0;
    left: 0; 
    background-color: rgb(255, 255, 255);
    padding: 10px 13px;
    -webkit-box-shadow: inset 0px 0px 20px 0px black;
            box-shadow: inset 0px 0px 20px 0px black;
    border-radius: 10px;
    -webkit-animation: welcomeSlideDown 3.5s linear forwards;
            animation: welcomeSlideDown 3.5s linear forwards; 

}



@-webkit-keyframes welcomeSlideDown{
    0%{top:-100%}
    25%{top:100px}
    30%{top:80px}
    35%{top:100px}
    40%{top:85px}
    45%{top:100px}
    50%{top:90px}
    55%{top: 100px}
    60%{top: 95px}
    65%{top: 100px}
    70%{top: 95px}
    80%{top: 100px}
    90%{top:95px}
    100%{top: 100px}
}



@keyframes welcomeSlideDown{
    0%{top:-100%}
    25%{top:100px}
    30%{top:80px}
    35%{top:100px}
    40%{top:85px}
    45%{top:100px}
    50%{top:90px}
    55%{top: 100px}
    60%{top: 95px}
    65%{top: 100px}
    70%{top: 95px}
    80%{top: 100px}
    90%{top:95px}
    100%{top: 100px}
}

@media(min-width: 768px){
    .section-one{
        height: 100vh; 
    }
    .welcome-section{
        width: 80%; 
        margin: 0 auto; 
    }
    .welcome-section p {
    padding: 39px;
    font-size: 20px;
    line-height: 47px;        
    }
} 

@media(min-width:800px) and (min-height: 600px){
    .section-one {
        height: 850px; 
    }
}

@media(min-width: 1024px){
    .welcome-section{
        width: 50%; 
    }
}
/*end of query*/

.info-div{
    background-color: rgba(72, 152, 255, 0.79);
    position: absolute;
    top: 308px;
    left: 0;
    right: 0;
    height: 169px;
    margin: 0 auto;
    width: 200px;
    overflow: hidden;
    border-radius: 7px;
    -webkit-box-shadow: inset 0px 0px 20px 5px #00f0ff;
            box-shadow: inset 0px 0px 20px 5px #00f0ff;
    opacity: 0;
    -webkit-animation: fadeInInfoDiv 2s linear forwards;
            animation: fadeInInfoDiv 2s linear forwards; 
}

@-webkit-keyframes fadeInInfoDiv{
    0%{opacity: 0}
    100%{opacity: 1}
}

@keyframes fadeInInfoDiv{
    0%{opacity: 0}
    100%{opacity: 1}
}

.info-wrapper{
    width: 700px; 
    position: absolute;
    left: calc(50% - 100px);
    -webkit-animation: animatedSlide 12s infinite;
            animation: animatedSlide 12s infinite; 
}

@-webkit-keyframes animatedSlide{

    25%{left:calc(50% - 100px)}
    50%{left:calc(50% - 300px)}
    75%{left:calc(50% - 500px)}

    100%{left:calc(50% - 100px)}
}

@keyframes animatedSlide{

    25%{left:calc(50% - 100px)}
    50%{left:calc(50% - 300px)}
    75%{left:calc(50% - 500px)}

    100%{left:calc(50% - 100px)}
}

.single-info-wrapper{
    display: inline-block;
    width: 180px;
    margin: 0 10px;
    text-align: center; 
}
.single-info-wrapper p {
    color: white; 
    font-size: 16px;
    font-weight: bold;    
}

.single-info-wrapper img {
    width: 100px;
    height: 103px;
    border-radius: 6px;
    border: solid 3px white;  
}

.single-info-wrapper a {
    text-decoration: none; 
    color: white; 
}


@media(min-width:768px){
    .info-div{ 
    top: 525px;
    height: 279px;
    width: 275px;        
    }
}






