.section-three { 
    background-image: url("../assets/images/shipwreck.jpg");
    background-attachment: fixed; 
    background-repeat: no-repeat;
    background-size: 100% 100%; 
    border-top: solid 1px black;    
    padding-bottom: 40px;  
}

.history-text{
    background-color: rgba(255, 255, 255, 0.81);
    margin: 28px 21px 0;
    padding: 5px 14px;
    border-radius: 4px;
}

.history-text p {
    line-height: 33px; 
}

@media(min-width:768px){
    .section-three{
        height: 50vh; 
    }
}


